<template>
    <el-menu class="navbar" mode="horizontal">
        <hamburger class="hamburger-container" :toggleClick="toggleSideBar" :isActive="sidebar.opened"></hamburger>
        <levelbar></levelbar>
        <el-dropdown class="avatar-container" trigger="click">
            <div class="avatar-wrapper">
                <!--<img class="user-avatar" :src="'https://avatars3.githubusercontent.com/u/22835301?v=4&s=40'">-->
                <i class="el-icon-caret-bottom"></i>
            </div>
            <el-dropdown-menu class="user-dropdown" slot="dropdown">
                <router-link class='inlineBlock' to="/">
                    <el-dropdown-item>
                        Home
                    </el-dropdown-item>
                </router-link>
                <el-dropdown-item divided><span @click="logout" style="display:block;">LogOut</span></el-dropdown-item>
            </el-dropdown-menu>
        </el-dropdown>
    </el-menu>
</template>

<script>
import { mapGetters } from 'vuex'
import Levelbar from './Levelbar'
import Hamburger from '@/components/Hamburger'

export default {
    components: {
        Levelbar,
        Hamburger
    },
    computed: {
        ...mapGetters([
          'sidebar',
          'avatar'
        ])
    },
    methods: {
        toggleSideBar() {
            this.$store.dispatch('ToggleSideBar')
        },
        logout() {
            this.$store.dispatch('LogOut').then(() => {
                location.reload()  // 为了重新实例化vue-router对象 避免bug
            })
        }
    }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    .navbar {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 38px;
        line-height: 38px;
        border-radius: 0px !important;

        .hamburger-container {
            line-height: 46px;
            height: 38px;
            float: left;
            padding: 0 10px;
        }
        .errLog-container {
            display: inline-block;
            position: absolute;
            right: 150px;
        }
        .screenfull {
            position: absolute;
            right: 90px;
            top: 16px;
            color: red;
        }
        .avatar-container {
            height: 30px;
            display: inline-block;
            position: absolute;
            right: 35px;
            .avatar-wrapper {
                cursor: pointer;
                margin-top: 3px;
                position: relative;
                .user-avatar {
                    width: 30px;
                    height: 30px;
                    border-radius: 10px;
                }
                .el-icon-caret-bottom {
                    position: absolute;
                    right: -20px;
                    top: 12px;
                    font-size: 12px;
                }
            }
        }
    }
</style>




