<template>
  <div class="app-wrapper" :class="{hideSidebar:!sidebar.opened}">
    <div class="sidebar-wrapper el-menu--dark">
        <sidebar class="sidebar-container"></sidebar>
    </div>
    <div class="main-container">
      <navbar></navbar>
      <app-main></app-main>
    </div>
  </div>
</template>


<script>
import { Navbar, Sidebar, AppMain } from '@/views/layout'

export default {
  name: 'layout',
  components: {
      Navbar,
      Sidebar,
      AppMain
  },
  computed: {
    sidebar() {
      return this.$store.state.app.sidebar
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    @import "src/styles/mixin.scss";

    .app-wrapper,
    .main-container {
        position: relative;
        width: 100%;
        height: 100%;
        overflow: hidden;
    }

    .app-wrapper {
        @include clearfix;
        padding-left: 200px;
        transition: all .28s ease-out;

        &.hideSidebar {
            padding-left: 40px;

            .sidebar-wrapper {
                transform: translate(-160px, 0);
                .sidebar-container {
                    transform: translate(152px, 0);
                }
                &:hover {
                    transform: translate(0, 0);
                    .sidebar-container {
                        transform: translate(0, 0);
                    }
                }
            }
        }
        .sidebar-wrapper {
            width: 200px;
            position: fixed;
            top: 0;
            bottom: 0;
            left: 0;
            z-index: 1001;
            overflow-x: hidden;
            overflow-y: auto;
        }
        .sidebar-wrapper,
        .sidebar-container {
            transition: all .28s ease-out;
        }

        .main-container {
            padding-top: 38px;
        }
    }
</style>



